import React from 'react';
import { postRequest } from "./functions/fetch.js"
import { FormLayout, ScreenSpinner, Select, Input, Group, Button, PanelHeader, Panel, Textarea, PanelHeaderBack } from '@vkontakte/vkui';


const requestUpdateURL = 'https://ambassador-todo.herokuapp.com/event/update'
const requestFindURL = 'https://ambassador-todo.herokuapp.com/event/ambassador'
const userRequestURL = "https://ambassador-todo.herokuapp.com/access/find"

const EditEvent = ({ fetchedUser, info, id, go }) => {

    const [nameEvent, setNameEvent] = React.useState();
    const [links, setLinks] = React.useState();
    const [description, setDescription] = React.useState();
    const [notes, setNotes] = React.useState();
    const [callback, setCallback] = React.useState();
    const [fetch, setFetch] = React.useState(true);
    const [user, setUser] = React.useState();
    const [eventInfo, setEventInfo] = React.useState();
    const [place, setPlace] = React.useState();
    const [participants, setParticipants] = React.useState();
    const [eventType, setEventType] = React.useState();
    const [participationForm, setParticipationForm] = React.useState();
    const [eventForm, setEventForm] = React.useState();

    if (fetch && fetchedUser != null) {
        const vkID = JSON.stringify({ "vkID": fetchedUser.id })
        postRequest('POST', userRequestURL, vkID)
            .then(data => {
                setUser(data[0].role)
                const eventID = JSON.stringify({ "_id": info })
                postRequest('POST', requestFindURL, eventID)
                    .then(data => {
                        setEventInfo(data[0])
                        setFetch(false)
                    })
            })
            .catch(err => console.log(err))
    }

    const onChangeNameEvent = (event) => {
        setNameEvent(event.target.value)
    }

    const onChangeCallback = (event) => {
        setCallback(event.target.value)
    }

    const onChangeLinks = (event) => {
        setLinks(event.target.value)
    }

    const onChangeDescription = (event) => {
        setDescription(event.target.value)
    }

    const onChangeNotes = (event) => {
        setNotes(event.target.value)
    }

    const onChangePlace = (event) => {
        setPlace(event.target.value)
    }

    const onChangeEventType = (event) => {
        setEventType(event.target.value)
    }

    const onChangeParticipants = (event) => {
        setParticipants(event.target.value)
    }

    const onChangeParticipationForm = (event) => {
        setParticipationForm(event.target.value)
    }

    const onChangeEventForm = (event) => {
        setEventForm(event.target.value)
    }

    const onClickForm = () => {
        let body = JSON.stringify({
            nameEvent: nameEvent,
            _id: eventInfo._id,
            description: description,
            participantsCallback: callback,
            publicationLinks: links,
            notes: notes,

            eventPlace: place,
            eventType: eventType,
            participants: participants,
            participationForm: participationForm,
            eventForm: eventForm,
        })
        postRequest('POST', requestUpdateURL, body)
            .catch(err => console.log(err))

    }

    if (fetch) {
        return (
            <Panel id={id}>
                <div style={{ display: 'flex', alignItems: 'center', flexDirection: 'column' }}>
                    <ScreenSpinner style={{ marginTop: '50%' }} />
                </div>
            </Panel>
        )
    }

    return (

        <Panel id={id}>

            <PanelHeader
                left={user === 'ambassador' ? <PanelHeaderBack style={{ color: "#fc2c38" }} onClick={go} data-to="events" /> : <PanelHeaderBack style={{ color: "#fc2c38" }} onClick={go} data-to="listambassador" />}>
                 </PanelHeader>
            {user === 'ambassador' ? <Group>
                <FormLayout>
                    <Input onChange={onChangeNameEvent} type="text" name="name" top=" " placeholder={eventInfo ? eventInfo.nameEvent : null} />
                    <Textarea onChange={onChangeDescription} name="description" top=" " placeholder={eventInfo ? eventInfo.description : null} />
                    <Input onChange={onChangeCallback} type="text" name="participants" top=" " placeholder={eventInfo ? eventInfo.participantsCallback : null} />
                    <Textarea onChange={onChangeLinks} name="links" top="  " placeholder={eventInfo ? eventInfo.publicationLinks : null} />
                    <Textarea onChange={onChangeNotes} name="notes" top="" placeholder={eventInfo ? eventInfo.notes : null} />

                    <Button style={{ backgroundColor: '#fc2c38' }} type='submit' size='xl' onClick={onClickForm} onMouseUp={go} data-to="events"></Button>

                </FormLayout>
            </Group> :
                <Group>
                    <FormLayout>
                        <Input onChange={onChangeNameEvent} type="text" name="name" top=" " placeholder={eventInfo ? eventInfo.nameEvent : null} />
                        <Textarea onChange={onChangeDescription} name="description" top=" " placeholder={eventInfo ? eventInfo.description : null} />
                        <Input onChange={onChangeCallback} type="text" name="participants" top=" " placeholder={eventInfo ? eventInfo.participantsCallback : null} />
                        <Textarea onChange={onChangeLinks} name="links" top="  " placeholder={eventInfo ? eventInfo.publicationLinks : null} />
                        <Textarea onChange={onChangeNotes} name="notes" top="" placeholder={eventInfo ? eventInfo.notes : null} />

                        <Select onChange={onChangeParticipationForm} top=" " placeholder={eventInfo ? eventInfo.participationForm : null} >
                                <option value=""> </option>
                                <option value=""> </option>
                                <option value="  ">  </option>
                            </Select>

                            <Select onChange={onChangeEventForm} top=" " placeholder={eventInfo ? eventInfo.eventForm : null} >
                                <option value=""></option>
                                <option value=""></option>
                            </Select>

                        {eventInfo.participationForm === '' ? <Group>
                            {eventInfo.eventForm === '' ? <Select onChange={onChangeEventType} top=" " placeholder={eventInfo ? eventInfo.eventType : null} >
                                <option value="/-">/-</option>
                                <option value=""></option>
                                <option value="  .">  .</option>
                                <option value=""></option>
                            </Select> : null}
                            {eventInfo.eventForm === '' ? <Select onChange={onChangeEventType} top=" " placeholder={eventInfo ? eventInfo.eventType : null}>
                                <option value=" "> </option>
                                <option value="/-">/-</option>
                                <option value="//">//</option>
                                <option value=""></option>
                                <option value=""></option>
                                <option value="  ">  </option>
                                <option value="  ">  </option>
                            </Select> : null}
                        </Group> : null}

                        {eventInfo.participationForm === '' ? <Group>
                            {eventInfo.eventForm === '' ? <Select onChange={onChangeEventType} top=" " placeholder={eventInfo ? eventInfo.eventType : null}>
                                <option value="   MRG  ">   MRG  </option>
                                <option value="/-">/-</option>
                                <option value="//">//</option>
                                <option value=" /   "> /   </option>
                                <option value="/">/</option>
                                <option value="   /">   /</option>
                                <option value="   ">   </option>
                                <option value=""></option>
                                <option value=""></option>
                                <option value="   ">   </option>
                            </Select> : null}
                            {eventInfo.eventForm === '' ? <Select onChange={onChangeEventType} top=" " placeholder={eventInfo ? eventInfo.eventType : null}>
                                <option value=" "> </option>
                                <option value="/-">/-</option>
                                <option value="//">//</option>
                                <option value=" /   "> /   </option>
                                <option value="/">/</option>
                                <option value=""></option>
                                <option value=" (, -, Alias)"> (, -, Alias)</option>
                                <option value=""></option>
                                <option value=""></option>
                                <option value=""></option>
                            </Select> : null}
                        </Group> : null}

                        {eventInfo.participationForm === '  ' ? <Group>
                            {eventInfo.eventForm === '' ? <Select onChange={onChangeEventType} top=" " placeholder={eventInfo ? eventInfo.eventType : null}>
                                <option value="   ( -, ,  ,  )">   ( -, ,  ,  )</option>
                                <option value="    /   ">    /   </option>
                                <option value=" /"> /</option>
                                <option value="    ">    </option>
                                <option value=""></option>
                            </Select> : null}
                            {eventInfo.eventForm === '' ? <Select onChange={onChangeEventType} top=" " placeholder={eventInfo ? eventInfo.eventType : null}>
                                <option value="   ( -, ,  ,  )">   ( -, ,  ,  )</option>
                                <option value=""></option>
                                <option value=" "> </option>
                                <option value=""></option>
                            </Select> : null}
                        </Group> : null}

                        <Input onChange={onChangePlace} type="text" name="name" top=" " placeholder={eventInfo ? eventInfo.eventPlace : null}/>
                        <Select onChange={onChangeParticipants} top=" " placeholder={eventInfo ? eventInfo.participants : null}>
                            <option value="1-29 ">1-29 </option>
                            <option value="30-99 ">30-99 </option>
                            <option value="100-299 ">100-299 </option>
                            <option value="300-999 ">300-999 </option>
                            <option value="1000  ">1000  </option>
                        </Select>

                        <Button style={{ backgroundColor: '#fc2c38' }} type='submit' size='xl' onClick={onClickForm} onMouseUp={go} data-to="events"></Button>

                    </FormLayout>
                </Group>}

        </Panel>
    )
}


export default EditEvent;